import Vue from 'vue'
import SafeAreaLayout from '@/common/layout/SafeAreaLayout.vue'
import NavigationWrapper from '@/views/wrapper/NavigationWrapper.vue'

Vue.directive('overflow', {
  inserted: function (el) {
    if (el.offsetWidth <= el.parentNode.offsetWidth) {
      el.classList.remove('is-overflow')
    } else {
      el.classList.add('is-overflow')
    }
  },
  componentUpdated: function (el, binding) {
    if (binding.value !== binding.oldValue) {
      if (el.classList.contains('is-overflow')) {
        el.classList.remove('is-overflow')
      }
      if (el.offsetWidth > el.parentNode.offsetWidth) {
        el.classList.add('is-overflow')
      }
    }
  }
})

Vue.component('safeAreaLayout', SafeAreaLayout)

Vue.component('navigationWrapper', NavigationWrapper)
